import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import '../styles/news.css';

// Resimleri import edin
import news2 from '../assets/images/news2.jpg';
import news3 from '../assets/images/news3.jpg';
import news4 from '../assets/images/news4.jpg';
import news5 from '../assets/images/news5.jpg';
import news6 from '../assets/images/news6.jpg';
import news7 from '../assets/images/news7.jpg';

const News = () => {
  const [newsList, setNewsList] = useState([]);

  // Sesli okuma fonksiyonu
  const speakText = (text) => {
    if ('speechSynthesis' in window) {
      window.speechSynthesis.cancel(); // Mevcut konumay iptal et
      const utterance = new SpeechSynthesisUtterance(text);
      window.speechSynthesis.speak(utterance);
    } else {
      alert('Taraycnz sesli okuma zelliini desteklemiyor.');
    }
  };

  useEffect(() => {
    const allNews = [
      {
        id: 2,
        title: 'Genler Deprem Bilincini Artryor: Eitim Kampanyas Balad!',
        summary: 'Son dnemde yaanan depremlerin ardndan, Genlik ve Spor Bakanl genlere ynelik bir deprem bilinlendirme kampanyas balatt.',
        image: news2,
      },
      {
        id: 3,
        title: 'Genler klim in Harekette: Byk Eyleme Hazrlk',
        summary:
          "Trkiyenin drt bir yanndan genler, iklim deiikliine dikkat ekmek iin bir araya geliyor.",
        image: news3,
      },
      {
        id: 4,
        title: 'KYK Borlarna Yeni Dzenleme: Genlere deme Kolayl!',
        summary: "Hazine ve Maliye Bakanl, KYK kredi borcu olan genlere ynelik yeni bir dzenleme aklad.",
        image: news4,
      },
      {
        id: 5,
        title: 'Yapay Zeka ve Blockchain: Genlik Atlyeleri Gndemde',
        summary:
          'Son haftalarda genlerin youn ilgi gsterdii Yapay Zeka ve Blockchain Atlyeleri, Trkiye genelinde teknolojiye merakl genleri bir araya getiriyor.',
        image: news5,
      },
      {
        id: 6,
        title: '2025 Seimleri: lk Kez Oy Kullanacak Genler in Bilgilendirme Yayn',
        summary: 'Yksek Seim Kurulu (YSK), 2025 ylnda ilk kez oy kullanacak 1.5 milyon gen iin bir rehber yaynlad. ',
        image: news6,
      },
      {
        id: 7,
        title: '2025te Genler in Daha Fazla Burs, Daha Fazla Frsat!',
        summary:
          'Genlik ve Spor Bakan yapt aklamada, 2025 yl iin genlere ynelik yeni frsatlarn hayata geirileceini duyurdu.',
        image: news7,
      },
    ];

    setNewsList(allNews);
  }, []);

  return (
    <div className="news-page">
      <h1 className="news-title">Haberler</h1>
      <div className="news-container" style={{ display: 'flex', flexWrap: 'wrap', gap: '20px' }}>
        {newsList.map((newsItem) => (
          <div
            key={newsItem.id}
            className="news-card"
            style={{
              width: '300px',
              height: '400px',
              border: '1px solid #ddd',
              borderRadius: '8px',
              overflow: 'hidden',
              display: 'flex',
              flexDirection: 'column',
            }}
          >
            <img
              src={newsItem.image}
              alt={newsItem.title}
              className="news-image"
              style={{ width: '100%', height: '180px', objectFit: 'cover' }}
            />
            <div className="news-content" style={{ padding: '15px', flexGrow: 1 }}>
              <h3 style={{ marginBottom: '10px', fontSize: '18px', color: '#333' }}>{newsItem.title}</h3>
              <p style={{ marginBottom: '15px', fontSize: '14px', color: '#555' }}>{newsItem.summary}</p>
              <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                <Link to={`/news/${newsItem.id}`}>
                  <button
                    className="news-link"
                    style={{
                      padding: '10px 20px',
                      backgroundColor: '#f17a0b',
                      color: 'white',
                      border: 'none',
                      borderRadius: '5px',
                      cursor: 'pointer',
                      textDecoration: 'none',
                    }}
                  >
                    Devamn Oku
                  </button>
                </Link>
                <span
                  onClick={() => speakText(`${newsItem.title}: ${newsItem.summary}`)}
                  onMouseEnter={() => speakText('Sesli okutmak iin buraya tklayn')}
                  style={{
                    cursor: 'pointer',
                    fontSize: '20px',
                    color: '#f17a0b',
                    display: 'inline-flex',
                    alignItems: 'center',
                  }}
                  title="Sesli Okuma"
                >
                  
                </span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default News;
