import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import news2 from '../assets/images/news2.jpg';
import news3 from '../assets/images/news3.jpg';
import news4 from '../assets/images/news4.jpg';
import news5 from '../assets/images/news5.jpg';
import news6 from '../assets/images/news6.jpg';
import news7 from '../assets/images/news7.jpg';

const NewsDetail = () => {
  const { id } = useParams();
  const [newsItem, setNewsItem] = useState(null);

  useEffect(() => {
    const newsData = [
      {
        id: 2,
        title: 'Genler Deprem Bilincini Artryor: Eitim Kampanyas Balad',
        content: `Son dnemde yaanan depremlerin ardndan, Genlik ve Spor Bakanl genlere ynelik bir deprem bilinlendirme kampanyas balatt.
                  <br /><br />
                  Kampanya kapsamnda, doru mdahale teknikleri, afet sonras dayanma ve ilk yardm eitimleri sunuluyor.
  <br /><br /> Etkinlik Detaylar:
		Tarih: 15-20 Ocak 2025
		Yer: Trkiye genelindeki genlik merkezleri.
Genler, kampanya sresince eitim alarak yerel gnll ekiplerine katlma frsat bulacak.`,
        image: news2,
      },
      {
        id: 3,
        title: 'Genler klim in Harekette: Byk Eyleme Hazrlk',
        content: `Trkiyenin drt bir yanndan genler, iklim deiikliine dikkat ekmek iin bir araya geliyor. Genlik klim Forumu, 25 Ocak 2025 tarihinde stanbulda dzenlenecek. Forumda, srdrlebilirlik ve evre koruma konularnda genlerin fikirleri tartlacak.
                  <br /><br />
                  Forum Detaylar:
		Tarih: 25 Ocak 2025
		Yer: stanbul Kongre Merkezi.
Etkinlie katlm cretsizdir. evrimii katlm iin kayt yapabilirsiniz. `,
        image: news3,
      },
      {
        id: 4,
        title: 'KYK Borlarna Yeni Dzenleme: Genlere deme Kolayl!',
        content: `Hazine ve Maliye Bakanl, KYK kredi borcu olan genlere ynelik yeni bir dzenleme aklad. Yeni dzenleme ile deme sreleri uzatlrken, ilk iki yl iin faiz alnmayacak.
                  <br /><br />
                  Genler iin nemli Maddeler:
		lk iki yl geri deme yaplmayabilecek.
		Faiz uygulamas tamamen kaldrld.
		Aylk deme tutarlar, genlerin gelir durumuna gre belirlenecek.`,
        image: news4,
      },
      {
        id: 5,
        title: 'Yapay Zeka ve Blockchain: Genlik Atlyeleri Gndemde',
        content: `Son haftalarda genlerin youn ilgi gsterdii Yapay Zeka ve Blockchain Atlyeleri, Trkiye genelinde teknolojiye merakl genleri bir araya getiriyor. Atlyelerde yeni balayanlar iin temel eitimler verilirken, ileri dzey projeler gelitirmek isteyenler iin zel mentor destei sunuluyor.
                  <br /><br />
                  Gndemden Not: lk eitim oturumu srasnda 500den fazla gen kayt yapt ve katlm youn bir ilgiyle devam ediyor.
Son kayt tarihi: 10 Ocak 2025. `,
        image: news5,
      },
      {
        id: 6,
        title: '2026 Seimleri: lk Kez Oy Kullanacak Genler in Bilgilendirme Yayn',
        content: `Yksek Seim Kurulu (YSK), 2026 ylnda ilk kez oy kullanacak 1.5 milyon gen iin bir rehber yaynlad. Bu rehberde, seim gn yaplmas gerekenler, oy kullanma sreci ve seim gvenlii hakknda bilgilere yer veriliyor.
                  <br /><br />
                  Rehber zeti:
		E-devlet zerinden semen kayd kontrol.
		Seim gn kurallar ve gvenlik nlemleri.
		Oy kullanma sreci ve nelere dikkat edilmesi gerektii.`,
        image: news6,
      },
      {
        id: 7,
        title: '2025te Genler in Daha Fazla Burs, Daha Fazla Frsat!',
        content: `Genlik ve Spor Bakan yapt aklamada, 2025 yl iin genlere ynelik yeni frsatlarn hayata geirileceini duyurdu.
                  <br /><br />
                 te yeni ylda hayata geirilecek projelerden bazlar:
		Burs Programlar: Daha fazla renciye ulaacak ekilde artrlacak.
		Spor Merkezleri: Her ehirde yeni genlik merkezleri alacak.
		Gnlllk Programlar: Genlerin sosyal projelere daha aktif katlmalar salanacak.
Detaylar yaknda aklanacak.`,
        image: news7,
      },
    ];

    const foundNews = newsData.find((news) => news.id === parseInt(id, 10));
    setNewsItem(foundNews || null);
  }, [id]);

  if (!newsItem) {
    return <div style={styles.notFound}>Haber bulunamad.</div>;
  }

  return (
    <div style={styles.newsDetail}>
      <div style={styles.header}>
        <img src={newsItem.image} alt={newsItem.title} style={styles.image} />
        <h1 style={styles.title}>{newsItem.title}</h1>
      </div>
      <div style={styles.content}>
        <p dangerouslySetInnerHTML={{ __html: newsItem.content }}></p>
      </div>
    </div>
  );
};

const styles = {
  newsDetail: {
    fontFamily: 'Arial, sans-serif',
    margin: '20px auto',
    maxWidth: '800px',
    padding: '20px',
    backgroundColor: '#f9f9f9',
    borderRadius: '8px',
    boxShadow: '0 2px 5px rgba(0, 0, 0, 0.1)',
  },
  header: {
    textAlign: 'center',
    marginBottom: '20px',
  },
  image: {
    maxWidth: '100%',
    height: 'auto',
    borderRadius: '8px',
    marginBottom: '15px',
  },
  title: {
    fontSize: '24px',
    fontWeight: 'bold',
    color: '#333',
    marginBottom: '10px',
  },
  content: {
    fontSize: '16px',
    lineHeight: '1.6',
    color: '#555',
  },
  notFound: {
    textAlign: 'center',
    fontSize: '20px',
    color: 'red',
    margin: '50px',
  },
};

export default NewsDetail;
