import React from 'react';

const KVKK = () => {
  return (
    <div style={{ padding: '20px', fontFamily: 'Arial, sans-serif' }}>
      <h1>KVKK Ak Rza Beyan</h1>
      <p>
        HedefGen platformu olarak, Kiisel Verilerin Korunmas Kanunu (KVKK) kapsamndaki haklarnz
        koruyoruz ve verilerinizin gvenliini nemsiyoruz.
      </p>
      <h2>Hangi Veriler Toplanr?</h2>
      <p>
        e-Devlet giriinizle paylatnz bilgiler (rnein ad, soyad, kimlik numaras) ve
        platformdaki aktiviteleriniz (katldnz etkinlikler, tercih ettiiniz alanlar) kaydedilir.
      </p>
      <h2>Veri leme Amalar</h2>
      <p>
        Verileriniz, platformumuzun size zelletirilmi kariyer nerileri sunmasn ve etkinlik
        katlm srelerini ynetmesini salamak amacyla ilenmektedir.
      </p>
      <h2>Haklarnz</h2>
      <p>
        KVKK kapsamnda, verilerinizin silinmesini, dzeltilmesini veya eriimini talep etme
        hakkna sahipsiniz. Bu haklarnz kullanmak iin{' '}
        <a href="/contact" style={{ color: '#007BFF' }}>
          letiim
        </a>{' '}
        sayfasndan bizimle iletiime geebilirsiniz.
      </p>
    </div>
  );
};

export default KVKK;
