import React, { useState, useEffect } from 'react';
import '../styles/Courses.css';

const mockCourses = [
  {
    id: 1,
    name: 'Siber Gvenlik Temelleri',
    category: 'Siber Gvenlik',
    image: require('../assets/images/siberguvenliktemelleri.jpg'),
    description: 'Siber gvenlik hakknda temel bilgiler.',
  },
  {
    id: 2,
    name: 'Etik Hacking',
    category: 'Siber Gvenlik',
    image: require('../assets/images/etikhacking.jpg'),
    description: 'Etik hacking dnyasna giri yapn.',
  },
  {
    id: 3,
    name: 'Zaman Ynetimi',
    category: 'Kiisel Geliim',
    image: require('../assets/images/zamanyonetimi.jpg'),
    description: 'Zamannz daha iyi nasl ynetebilirsiniz?',
  },
  {
    id: 4,
    name: 'Takm Ynetimi',
    category: 'Kiisel Geliim',
    image: require('../assets/images/takimyonetimi.jpg'),
    description: 'Ekiplerinizi etkin ekilde ynetin.',
  },
  {
    id: 5,
    name: 'HTML ve CSS',
    category: 'Web Gelitirme',
    image: require('../assets/images/htmlvecss.jpg'),
    description: 'HTML ve CSS ile web gelitirmeye balayn.',
  },
  {
    id: 6,
    name: 'JavaScript Temelleri',
    category: 'Web Gelitirme',
    image: require('../assets/images/javascriptemelleri.jpg'),
    description: 'JavaScript ile temel programlama renin.',
  },
  {
    id: 7,
    name: 'A Gvenlii',
    category: 'Siber Gvenlik',
    image: require('../assets/images/agguvenligi.jpg'),
    description: 'A gvenlii tekniklerine giri yapn.',
  },
  {
    id: 8,
    name: 'Kiisel Verimlilik',
    category: 'Kiisel Geliim',
    image: require('../assets/images/kisiselverimlilik.jpg'),
    description: 'Gnlk hayatnz daha verimli hale getirin.',
  },
  {
    id: 9,
    name: 'React.js ile Web Gelitirme',
    category: 'Web Gelitirme',
    image: require('../assets/images/reactjsilewebgelistirme.jpg'),
    description: 'React.js ile modern web uygulamalar gelitirin.',
  },
];


const Courses = () => {
  const [selectedCategory, setSelectedCategory] = useState('Tm');
  const [appliedCourses, setAppliedCourses] = useState([]);

  // Bavurulan kurslar Local Storage'dan ykle
  useEffect(() => {
    const storedApplications = JSON.parse(localStorage.getItem('appliedCourses')) || [];
    setAppliedCourses(storedApplications);
  }, []);

  // Local Storage'a bavurular kaydet
  const saveApplicationsToStorage = (applications) => {
    localStorage.setItem('appliedCourses', JSON.stringify(applications));
  };

  const handleApply = (courseId) => {
    alert('Bavurunuz alnmtr! Sizinle en ksa srede iletiime geilecektir.');
    const updatedApplications = [...appliedCourses, courseId];
    setAppliedCourses(updatedApplications);
    saveApplicationsToStorage(updatedApplications); // Bavuruyu Local Storage'a kaydet
  };

  const filteredCourses =
    selectedCategory === 'Tm'
      ? mockCourses
      : mockCourses.filter((course) => course.category === selectedCategory);

  return (
    <div className="courses-page">
      <div className="filter-buttons">
        {['Tm', 'Siber Gvenlik', 'Kiisel Geliim', 'Web Gelitirme'].map((category) => (
          <button
            key={category}
            className={selectedCategory === category ? 'active' : ''}
            onClick={() => setSelectedCategory(category)}
          >
            {category}
          </button>
        ))}
      </div>

      <div className="courses-container">
        {filteredCourses.map((course) => (
          <div key={course.id} className="course-card">
            <img src={course.image} alt={course.name} />
            <div className="course-card-content">
              <h3>{course.name}</h3>
              <p>{course.description}</p>
              <button
                className="apply-button"
                onClick={() => handleApply(course.id)}
                disabled={appliedCourses.includes(course.id)}
                style={{
                  backgroundColor: appliedCourses.includes(course.id) ? '#747474' : '',
                  color: appliedCourses.includes(course.id) ? 'white' : '',
                  cursor: appliedCourses.includes(course.id) ? 'not-allowed' : 'pointer',
                }}
              >
                {appliedCourses.includes(course.id) ? 'Bavuruldu' : 'Bavur'}
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Courses;
