import React, { useState } from 'react';
import '../styles/careerpath.css';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const CareerPath = () => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [appliedItems, setAppliedItems] = useState([]);
  const [goals, setGoals] = useState([
    { id: 4, text: 'Siber Gvenlik Uzman', completed: false, isAdminControlled: false },
    { id: 3, text: 'Siber Gvenlik altayna katl', completed: false, isAdminControlled: false },
    { id: 2, text: 'En az 2 konferansa git', completed: true, isAdminControlled: true },
    { id: 1, text: 'Siber Gvenlik Temelleri eitimini izle', completed: true, isAdminControlled: true },
  ]);
  const handleDragEnd = (result) => {
    const { destination, source } = result;

    if (!destination) return; // Eer srklenen e geerli bir yere braklmadysa k
    if (destination.index === source.index) return; // Ayn yere brakldysa hibir ey yapma

    // Siber Gvenlik Uzmann korumak iin kontrol
    const draggedGoal = goals[source.index];
    if (draggedGoal.text === 'Siber Gvenlik Uzman') return;

    const reorderedGoals = Array.from(goals);

    // Siber Gvenlik Uzman en stte kalacak
    const uzmanGoal = reorderedGoals.find((goal) => goal.text === 'Siber Gvenlik Uzman');
    const uzmanIndex = reorderedGoals.indexOf(uzmanGoal);

    // eyi yeniden sralyoruz
    const [removed] = reorderedGoals.splice(source.index, 1);
    reorderedGoals.splice(destination.index, 0, removed);

    setGoals(reorderedGoals);
  };

  const [newGoalText, setNewGoalText] = useState('');
  const [showAddGoalInput, setShowAddGoalInput] = useState(false);
  const [popupMessage, setPopupMessage] = useState('');
  const [showPopup, setShowPopup] = useState(false);

  const handleApply = (item) => {
    if (appliedItems.includes(item)) {
      setPopupMessage('Zaten bavurdunuz!');
    } else {
      setPopupMessage('Bavurunuz alnmtr! Sizinle en ksa srede iletiime geilecektir.');
      setAppliedItems([...appliedItems, item]);
    }
    setShowPopup(true);

    setTimeout(() => {
      setShowPopup(false);
    }, 3000);
  };

  // CV ykleme fonksiyonu
  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    setSelectedFile(file);
    alert(`${file.name} baaryla yklendi.`);
  };

  // Yeni hedef ekleme
  const handleAddGoal = () => {
    if (newGoalText.trim() === '') {
      alert('Ltfen bir hedef giriniz.');
      return;
    }
    const newGoal = { id: Date.now(), text: newGoalText, completed: false, isAdminControlled: false };

    const uzmanIndex = goals.findIndex((goal) => goal.text === 'Siber Gvenlik Uzman');

    const updatedGoals = [...goals];
    updatedGoals.splice(uzmanIndex + 1, 0, newGoal);
  
    setGoals(updatedGoals);
    setNewGoalText('');
    setShowAddGoalInput(false);
  };



  const toggleCompletion = (id) => {
    setGoals(goals.map((goal) =>
      goal.id === id ? { ...goal, completed: !goal.completed } : goal
    ));
  };
  return (
    <div className="career-path">
      {/* Sol Taraf */}
      <div className="left-section">
        <h2>Tam Sana Uygun</h2>

        <div className="section">
          <h3>Kurslar</h3>
          <div className="items-grid">
            <div className="item-card">
              <h4>Siber Gvenlik 101</h4>
              <p>Tarih: 01.01.2025</p>
              <p>Saat: 15:00</p>
              <button
                className={appliedItems.includes('Siber Gvenlik 101') ? 'applied' : ''}
                onClick={() => handleApply('Siber Gvenlik 101')}
                disabled={appliedItems.includes('Siber Gvenlik 101')}
              >
                {appliedItems.includes('Siber Gvenlik 101') ? 'Bavurdun' : 'Bavur'}
              </button>
            </div>
            <div className="item-card">
              <h4>Web Gelitirme</h4>
              <p>Tarih: 02.01.2025</p>
              <p>Saat: 18:00</p>
              <button
                className={appliedItems.includes('Web Gelitirme') ? 'applied' : ''}
                onClick={() => handleApply('Web Gelitirme')}
                disabled={appliedItems.includes('Web Gelitirme')}
              >
                {appliedItems.includes('Web Gelitirme') ? 'Bavurdun' : 'Bavur'}
              </button>
            </div>
          </div>
        </div>

        <div className="section">
          <h3>Konferanslar</h3>
          <div className="items-grid">
            <div className="item-card">
              <h4>AI Konferans</h4>
              <p>Tarih: 03.01.2025</p>
              <p>Saat: 14:00</p>
              <button
                className={appliedItems.includes('AI Konferans') ? 'applied' : ''}
                onClick={() => handleApply('AI Konferans')}
                disabled={appliedItems.includes('AI Konferans')}
              >
                {appliedItems.includes('AI Konferans') ? 'Bavurdun' : 'Bavur'}
              </button>
            </div>
            <div className="item-card">
              <h4>Blockchain Teknolojisi</h4>
              <p>Tarih: 05.01.2025</p>
              <p>Saat: 10:00</p>
              <button
                className={appliedItems.includes('Blockchain Teknolojisi') ? 'applied' : ''}
                onClick={() => handleApply('Blockchain Teknolojisi')}
                disabled={appliedItems.includes('Blockchain Teknolojisi')}
              >
                {appliedItems.includes('Blockchain Teknolojisi') ? 'Bavurdun' : 'Bavur'}
              </button>
            </div>
          </div>
        </div>

        <div className="section">
          <h3>altaylar</h3>
          <div className="items-grid">
            <div className="item-card">
              <h4>Siber Gvenlik altay</h4>
              <p>Tarih: 10.01.2025</p>
              <p>Saat: 09:00</p>
              <button
                className={appliedItems.includes('Siber Gvenlik altay') ? 'applied' : ''}
                onClick={() => handleApply('Siber Gvenlik altay')}
                disabled={appliedItems.includes('Siber Gvenlik altay')}
              >
                {appliedItems.includes('Siber Gvenlik altay') ? 'Bavurdun' : 'Bavur'}
              </button>
            </div>
            <div className="item-card">
              <h4>Yapay Zeka altay</h4>
              <p>Tarih: 12.01.2025</p>
              <p>Saat: 11:00</p>
              <button
                className={appliedItems.includes('Yapay Zeka altay') ? 'applied' : ''}
                onClick={() => handleApply('Yapay Zeka altay')}
                disabled={appliedItems.includes('Yapay Zeka altay')}
              >
                {appliedItems.includes('Yapay Zeka altay') ? 'Bavurdun' : 'Bavur'}
              </button>
            </div>
          </div>
        </div>

        <div className="cv-actions">
          <button onClick={() => document.getElementById('file-upload').click()}>CV Ykle</button>
          <input
            type="file"
            id="file-upload"
            accept=".pdf"
            style={{ display: 'none' }}
            onChange={handleFileUpload}
          />
          <button>CV Olutur</button>
        </div>
      </div>

      {/* Sa Taraf */}
      <div className="right-section">
        <h2>Hedeflerim</h2>
        <DragDropContext onDragEnd={handleDragEnd}>
        <Droppable droppableId="goals">
          {(provided) => (
            <div className="roadmap" {...provided.droppableProps} ref={provided.innerRef}>
              {goals.map((goal, index) => (
                <Draggable key={goal.id} draggableId={goal.id} index={index}>
                  {(provided, snapshot) => (
                    <div
                      className={`roadmap-item ${
                        goal.completed ? 'completed' : ''
                      } ${snapshot.isDragging ? 'dragging' : ''}`}
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                    >
                      <div
                        className={`goal-icon ${
                          goal.completed ? 'completed-icon' : ''
                        }`}
                        onClick={() => toggleCompletion(goal.id)}
                      />
                      <span>{goal.text}</span>
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
        <div className="goal-actions">
          {showAddGoalInput ? (
            <div className="add-goal">
              <input
                type="text"
                placeholder="Yeni hedef giriniz"
                value={newGoalText}
                onChange={(e) => setNewGoalText(e.target.value)}
              />
              <button onClick={handleAddGoal}>Ekle</button>
            </div>
          ) : (
            <button onClick={() => setShowAddGoalInput(true)}>Hedef Ekle</button>
          )}
        </div>
      </div>

      {showPopup && <div className="popup">{popupMessage}</div>}
    </div>
  );
};

export default CareerPath;

